.container {
  display: inline-block;
  position: relative;
}

.subContainer {
  position: relative;
  margin: var(--spacing-m);
}

.svgElement {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  circle {
    stroke-width: calc(var(--size-xxs) * 2) !important;
    animation: dash 1s ease-in-out forwards;
  }
}

@keyframes dash {
  from {
    stroke-dashoffset: 1100;
  }
}