@import '../../const.scss';

$markerColor: #8f97a9;
$pipeWidth: 5px;

.pipePseudoElement {
  content: "";
  box-sizing: border-box;
  z-index: 0;
  position: absolute;
  left: calc(var(--spacing-s) + var(--size-m) / 2 - #{$pipeWidth});
  width: 0;
  background: $markerColor;
  height: 100%;
  border-left: $pipeWidth solid $markerColor;
  border-right: $pipeWidth solid $markerColor;
  box-shadow: var(--box-shadow-default);
}

.container {
  display: block;
  > *:not(:first-child) {
    position: relative;
    z-index: 1;
    &:before {
      @extend .pipePseudoElement;
    }
    &:first-child:before {
      top: 50%;
      height: 50%;
    }
  }
}

.contentContainerSubtitle {
  font-size: var(--size-text-s);
  color: var(--color-text-secondary);
  margin: var(--spacing-l) 0 0 0;
  text-align: center;
  > * > * {
    vertical-align: middle;
  }
}

.sub {
  font-size: var(--size-text-s);
  color: var(--color-text-secondary);
  text-align: center;
}

.markerYear {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: var(--size-m);
  text-align: center;
  // box-shadow: var(--box-shadow-default);
  font-size: var(--size-text-s);
  border-radius: 1em;
  padding: var(--spacing-xs) 0;
  margin: var(--spacing-xs) 0 var(--spacing-s) var(--spacing-s);
  background-color: $markerColor;
}

.titleContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  text-align: left;
  font-size: var(--size-text-l);
  font-weight: bold;
  color: var(--color-text-primary);
  min-height: var(--size-m);
  padding-left: calc(var(--size-m) + 2 * var(--spacing-s));
  padding-right: calc(var(--spacing-s) + var(--spacing-m));
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
  cursor: pointer;
  user-select: none;
  transition: all .3s ease;
  text-decoration: none;
  outline: none;
  &:hover {
    padding-left: calc(var(--size-m) + 2 * var(--spacing-s) + var(--spacing-m));
    padding-right: var(--spacing-s);
    color: var(--color-text-emphasis) !important;
    .markerMonth {
      transform: scale(1.1);
    }
  }
}

.titleWrapper {
  display: inline-flex;
  line-height: var(--size-m);
  margin: var(--spacing-s) 0;
  color: var(--color-text-emphasis);
  > * {
    display: inline;
    vertical-align: middle;
    line-height: 1.4;
  }
  > *:first-child {
    display: inline-flex;
    align-self: center;
  }
}

.markerMonth {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: var(--spacing-s);
  // box-shadow: var(--box-shadow-default);
  font-size: var(--size-text-s);
  border-radius: var(--size-m);
  width: var(--size-m);
  // height: calc(100% - 2 * var(--spacing-s));
  height: var(--size-m);
  background-color: $markerColor;
  transition: all .3s ease;
}

.secondaryTitleContainer {
  font-size: var(--size-text-m);
  padding-top: 0;
  padding-bottom: 0;
  .markerMonth {
    font-size: var(--size-text-xs);
    width: var(--size-s);
    height: var(--size-s);
    top: calc((var(--size-m) - var(--size-s)) / 2);
    left: calc(var(--spacing-s) + (var(--size-m) - var(--size-s)) / 2);
  }
}

.contentContainer {
  position: relative;
  background-color: var(--color-background-secondary);
  // box-shadow: var(--box-shadow-default-inset);
  padding: var(--spacing-m);
  padding-bottom: var(--spacing-xl);
  opacity: 1;
  &.collapsed {
    padding-bottom: 0;
    padding-top: 0;
    opacity: 0;
    max-height: 0;
  }
  &:after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    left: 50%;
    width: 100vw;
    transform: translateX(-50%);
    background-color: var(--color-background-secondary);
    z-index: 0;
  }
  &:before {
    @extend .pipePseudoElement;
    top: calc(-1 * var(--spacing-m));
    height: calc(100% + var(--spacing-m));
    box-shadow: none;
    opacity: .05;
    z-index: 1;
  }
  > * {
    position: relative;
    z-index: 2;
  }
  transition: all .35s;
}

.timelineItemMinor {
  .titleContainer {
    font-size: var(--size-text-m);
    font-weight: normal;
    padding-top: 0;
    padding-bottom: 0;
    .titleWrapper {
      color: var(--color-text-minor);
    }
  }
  .markerMonth {
    width: var(--size-s);
    height: var(--size-s);
    font-size: var( --size-text-xs);
    // height: calc(var(--size-s) + var(--spacing-s));
    left: calc(var( --size-m) / 2 + var(--spacing-s) - var(--size-s) / 2);
    // top: calc(var(--size-s) / 2 - #{$pipeWidth} - var(--spacing-s) / 2);
  }
}

.timelineItemSemiMajor .titleContainer .titleWrapper {
  color: var(--color-text-emphasis);
}

.timelineHeader {
  overflow: visible;
  // height: 0;
  margin-top: var(--spacing-xl);
  margin-bottom: 0;
  @extend .title-h2;
}
