:root, [data-theme="default"] {
  --color-text-emphasis: #fff;
  --color-text-primary: #eee;
  --color-text-secondary: #d3d3d3;
  --color-text-minor: #aaa;

  --color-background-primary: #282c34;
  --color-background-secondary: #363c44;

  --color-positive: #bcec9f;
  --color-negative: #ff7b7b;
  --color-selected: #8282ff;

  --size-text-xs: calc(8px);
  --size-text-s: calc(12px + .1vmin);
  --size-text-m: calc(14px + .3vmin);
  --size-text-l: calc(16px + .5vmin);
  --size-text-xl: calc(21px + .8vmin);
  --size-text-xxl: calc(24px + 1vmin);

  --size-xxs: calc(6px + .25vmin);
  --size-xs: calc(8px + .5vmin);
  --size-s: calc(16px + 1vmin);
  --size-m: calc(24px + 2vmin);
  --size-l: calc(36px + 3vmin);
  --size-xl: calc(48px + 4vmin);
  --size-xxl: calc(72px + 5vmin);
  --size-xxxl: calc(108px + 8vmin);
  --size-xxxxl: calc(144px + 12vmin);

  --spacing-xs: calc(2px + .1vmin);
  --spacing-s: calc(4px + .2vmin);
  --spacing-m: calc(8px + .4vmin);
  --spacing-l: calc(16px + .6vmin);
  --spacing-xl: calc(26px + .9vmin);

  --box-shadow-default: 0 2px 4px rgba(40, 44, 52, .3);
  --box-shadow-intensive: 0 2px 4px rgba(40, 44, 52, .8);
  --box-shadow-default-inset: inset 0 0 4px rgba(40, 44, 52, .3);

  --max-page-width: 850px;
}

:root {
  background-color: var(--color-background-primary);
  color: var(--color-text-primary);
  font-size: var(--size-text-m);
  text-align: justify;
}

a {
  text-decoration: underline;
  color: inherit;
}

h1, .title-h1 {
  font-weight: bold;
  text-align: center;
  font-size: var(--size-text-xxl);
  margin: var(--spacing-xl) 0 var(--spacing-xl) 0;
}

h2, .title-h2 {
  font-weight: bold;
  text-align: center;
  font-size: var(--size-text-xl);
  margin: var(--spacing-l) 0 var(--spacing-l) 0;
}

h3, .title-h3 {
  font-weight: bold;
  text-align: center;
  font-size: var(--size-text-l);
  margin: var(--spacing-m) 0 var(--spacing-m) 0;
}

[data-theme="dark"] {
  --color-text-primary: red;
}

:export {
  max-page-width: var(--max-page-width);
}