:export {
  --fillColor: var(--color-positive);
  --selectedColor: var(--color-selected);
  --strokeColor: var(--color-text-emphasis);
  --strokeWidth: 3;
}

.mapContainer {
  position: relative;
  color: var(--color-text-minor);
}

.mapIcon {
  width: 100%;
  path[data-name] {
    cursor: pointer;
    transition: all .1s ease-out;
    &:hover {
      fill: var(--color-text-emphasis) !important;
    }
  }
}
