.contentContainer {
  overflow: hidden;
  line-height: 0;
  position: relative;
  text-align: center;
  box-sizing: border-box;
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-box-pack: center;

  > * {
    max-height: calc(100vh - 94px);
    max-width: 100vw;
    height: auto;
    user-select: none;
    box-sizing: border-box;
    line-height: normal;
  }
}

.embeddedVideo {
  position: relative;
  width: 100%;
  height: 0px;
  padding-bottom: 56.25%; // 16:9
  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.sub {
  font-size: var(--size-text-xs);
  color: var(--color-text-secondary);
}

