@import '../../const.scss';

.container {
  display: table;
  margin: 0 auto;
}

.title {
  @extend .title-h2;
}

.item {
  display: flex;
  align-items: center;
  flex-direction: row;
  background-color: var(--color-background-secondary);
  padding: var(--spacing-s) var(--spacing-l) var(--spacing-s) var(--spacing-s);
  margin: var(--spacing-s) var(--spacing-m);
  border-radius: 100px;
  box-sizing: border-box;
  user-select: none;
  text-decoration: none;
  outline: none;
  color: inherit;
}

.icon {
  margin: 0;
  margin-right: var(--spacing-s);
  vertical-align: center;
}

.selectedItem {
  color: var(--color-text-emphasis) !important;
}

.caption {
  @extend .title-h3;
  margin: 0;
  padding: 0;
  text-align: inherit;
  font-size: inherit;
}

.description {
  margin: 0;
  font-size: var(--size-text-s);
  color: var(--color-text-secondary);
}

.svgDownscaled80 {
  > svg {
    width: 80%;
    height: 80%;
  }
}

.icon-panorama {
  @extend .svgDownscaled80;
}