.gallery {
  overflow: hidden;
  margin: var(--spacing-l) 0;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.link {
  position: absolute;
  cursor: pointer;
  transition: transform .3s ease;
  &:hover {
    transform: scale(0.95);
  }
}

.thumbnailCenterIcon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
}