.container {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-emphasis);
  margin-bottom: var(--spacing-xl);
  > * {
    vertical-align: middle;
  }
}

.centerContainer {
  margin: 0 var(--spacing-m);
  min-width: calc(2 * var(--size-xl) + 2 * var(--spacing-m));
}

.text {
  position: relative;
  top: -0.2em;
  text-align: center;
}

.dotsContainer {
  text-align: center;
  line-height: 0;

  > * {
    cursor: pointer;
    display: inline-block;
    width: var(--size-xxs);
    height: var(--size-xxs);
    border-radius: var(--size-xxs);
    background-color: var(--color-text-minor);
    margin: 0 var(--spacing-xs);
  }
}

.selectedDot {
  background-color: var(--color-text-emphasis);
}
