// Most icons source: https://simpleicons.org

.icon {
  position: relative;
  box-sizing: border-box;
  align-self: auto;
  vertical-align: middle;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all .3s ease;
  > svg {
    width: 100%;
    height: 100%;
    position: relative;
  }
  &.shadow {
    filter: drop-shadow(var(--box-shadow-intensive));
  }
}

.margin-s {
  margin: 0 var(--spacing-s) 0 var(--spacing-s);
}

.margin-xs {
  margin: 0 var(--spacing-xs) 0 var(--spacing-xs);
}

.margin-none {
  margin: 0;
}

.svgRounded > svg {
  width: 70%; // Sin 45 deg
  height: 70%;
}

.rounded {
  border-radius: 1000px;
  box-shadow: var(--box-shadow-default);
  @extend .svgRounded;
}

.hoverable {
  cursor: pointer;
  &:hover {
    // margin: 0 14px 0 14px;
    transform: scale(0.9);
  }
  user-select: none;
}

.size-xxs {
  width: var(--size-xxs); min-width: var(--size-xxs);
  height: var(--size-xxs); min-height: var(--size-xxs);
}

.size-xs {
  width: var(--size-xs); min-width: var(--size-xs);
  height: var(--size-xs); min-height: var(--size-xs);
}

.size-s {
  width: var(--size-s); min-width: var(--size-s);
  height: var(--size-s); min-height: var(--size-s);
}

.size-m {
  width: var(--size-m); min-width: var(--size-m);
  height: var(--size-m); min-height: var(--size-m);
}

.size-l {
  width: var(--size-l); min-width: var(--size-l);
  height: var(--size-l); min-height: var(--size-l);
}

.size-xl {
  width: var(--size-xl); min-width: var(--size-xl);
  height: var(--size-xl); min-height: var(--size-xl);
}

.size-xxl {
  width: var(--size-xxl); min-width: var(--size-xxl);
  height: var(--size-xxl); min-height: var(--size-xxl);
}

.size-xxxl {
  width: var(--size-xxxl); min-width: var(--size-xxxl);
  height: var(--size-xxxl); min-height: var(--size-xxxl);
}
