body {
  // overflow: auto;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  overflow: hidden;
  > * {
    min-height: 100vh;
  }
}

.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #09d3ac;
}

.pageContentContainer {
  max-width: var(--max-page-width);
  margin: 0 auto;
}

.socialIcons {
  margin: var(--spacing-xl) 0;
}