.socialIcon {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  vertical-align: top;
  margin: 0;
  padding: 0;
  // Overwrite for <a>
  text-decoration: none;
  color: inherit;
  outline: none;
  > :first-child:hover + .iconFooter {
    margin-top: var(--spacing-s);
    margin-bottom: 0;
  }
}

.iconFooter {
  font-size: var(--size-text-xs);
  color: var(--color-text-secondary);
  margin: var(--spacing-xs);
  transition: margin .3s ease;
  > * {
    vertical-align: middle;
  }
  > *:last-child {
    margin-right: var(--spacing-xs);
  }
}

.svgDownscaled80 {
  > svg {
    width: 80%;
    height: 80%;
  }
}

.icon-html5 {
  @extend .svgDownscaled80;
}

.icon-css3 {
  @extend .svgDownscaled80;
}

.icon-rabbitmq {
  @extend .svgDownscaled80;
}

.icon-d3js {
  @extend .svgDownscaled80;
}

.icon-windows {
  @extend .svgDownscaled80;
}

.icon-algorand {
  @extend .svgDownscaled80;
}