$avatarBaseSize: 144px;
$avatarExtraSize: 12vmin;

.strip {
  position: relative;
  padding-bottom: calc(#{$avatarBaseSize / 2} + #{$avatarExtraSize / 2});
}

.avatar {
  position: absolute;
  box-sizing: border-box;
  height: calc(#{$avatarBaseSize} + #{$avatarExtraSize});
  width: calc(#{$avatarBaseSize} + #{$avatarExtraSize});
  top: calc(#{-$avatarBaseSize / 2} - #{$avatarExtraSize / 2});
  left: calc(50% - #{$avatarBaseSize / 2} - #{$avatarExtraSize / 2});
  border-radius: 10000px;
  box-shadow: var(--box-shadow-default);
  border: 3px solid white;
  background-size: 100%;
}

.slide-0-avatar {
  background-image: url(./slide-0-avatar.jpg);
}

.slide-1-avatar {
  background-image: url(./slide-1-avatar.jpg);
}

.slide-2-avatar {
  background-image: url(./slide-2-avatar.jpg);
}

.slide-3-avatar {
