@import '../../const.scss';

.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.title {
  @extend .title-h2;
}

.table {
  > .row {
    display: table-row;
    > .column {
      display: table-cell;
      vertical-align: middle;
      padding: 2px 0;
    }
  }
}

.detailedColumn {
  vertical-align: top;
}

.icon {
  margin: 0 var(--spacing-m);
}

.sub {
  color: var(--color-text-minor);
  font-size: var(--size-text-s);
}