.head {
  position: relative;
  overflow: hidden;
  height: 30vh;
  box-shadow: var(--box-shadow-default);
}

.cover {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: 50%;
  transform: translateY(0);
  background-image: url(./slide-0-background.jpg);
  // transition: transform .1s ease;
}

